'use client';
import { useState } from 'react';
import './CSS/preguntas.css';
const faqs = [
  {
    question: "¿Cómo puedo registrarme en la plataforma?",
    answer: "Para registrarte, haz clic en el botón de 'Registro' en la página principal e ingresa tus datos."
  },
  {
    question: "¿Dónde puedo ver las noticias más recientes?",
    answer: "En la sección de 'Noticias' encontrarás un carrusel con las últimas novedades políticas en México."
  },
  {
    question: "¿Cómo se cuentan los votos?",
    answer: "Los votos se registran en la base de datos de Supabase y se muestran de forma segura y transparente."
  },
  {
    question: "¿Qué hago si olvido mi contraseña?",
    answer: "Puedes recuperarla desde la página de inicio de sesión, haciendo clic en 'Olvidé mi contraseña'."
  }
];

export default function PreguntasPage() {
  const [openIndex, setOpenIndex] = useState<number | null>(null);

  return (
    <div className="faq-container">
      <h1>Preguntas Frecuentes</h1>
      <div className="faq-list">
        {faqs.map((faq, idx) => (
          <div key={idx} className="faq-item">
            <button
              className="faq-question"
              onClick={() => setOpenIndex(openIndex === idx ? null : idx)}
            >
              {faq.question}
            </button>
            {openIndex === idx && <p className="faq-answer">{faq.answer}</p>}
          </div>
        ))}
      </div>
    </div>
  );
}
